*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


.headerContainer{
    display: flex;
    align-items: center;
    padding: 15px;
}
.menuContainer{
    width: 100%;
}

.menu{
    display: flex;
    justify-content: flex-end;
}

ul{
    gap: 20px;
}

li{
    list-style-type: none;
    font-size: 20px;
    cursor: pointer;
}

a{
    text-decoration: none;
    color: black;
}


/*restaurantCard*/

.body{
    padding: 15px;
}
.res-container{
    border: 1px solid black;
    width: 250px;
    height: 250px;
    padding: 10px
}

.res-container:hover{
    border: 1px solid lightgrey;
}

/*search input*/
.searchContainer{
    display: flex;
    padding: 10px 10px 10px 0px;
    gap: 5px;
}

.searchBtn {
    width: auto;
    padding: 7px;
}

.inputText{
    padding: 7px;
}

.logoText{
    background-color: rgb(245, 238, 238);
    cursor: pointer;
}